import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Row, Col, Toast } from 'react-bootstrap';
import DatePicker from "react-datepicker";
import { useFormik } from 'formik';
import memberDetails from "../../../models/memberDetails"
import masterRecord from '../../../models/masterRecord';

import Loader from '../../layout/Loader';

//import "react-datepicker/dist/react-datepicker.css";
function AddMember(props) {
    const [enrollmentDate, setEnrollmentDate] = useState(new Date());
    const [dob, setDob] = useState();

    const [values, setValues] = useState({});
    const [isShowLoader, setisShowLoader] = useState(false)
    const [groups, setGroups] = useState([])
    const [showToast, setShowToast] = useState({ isShow: false, type: "", message: "" })
    useEffect(() => {
        getMemberGroups();
    }, [])
    const getMemberGroups = async () => {
        try {
            setisShowLoader(true);
            let response = await masterRecord.MemberGroupModel.getMemberGroups();
            setisShowLoader(false);
            setGroups(response.body.message)
        } catch (error) {
            setisShowLoader(false);
            setShowToast({ isShow: true, type: "bg-danger", message: error.response.body.message })
        }
    }
    const validate = values => {
        const errors = {};
        if (!values.member_name) {
            errors.member_name = 'Please provide member name';
        }
        return errors;
    };
    const formik = useFormik({
        initialValues: {
            enrollment_date: enrollmentDate,
            date_of_birth: dob,
            member_name: '',
            gender: "",
            age: "",
            marital_status: "",
            mobile_number: "",
            email_id: "",
            member_group_id: "",
            aadhar_number: "",
            pan_card_number: "",
            driving_license_number: "",
            voter_id_number: "",
            ration_card_number: ""
        },
        validate,
        onSubmit: async (values) => {
            setisShowLoader(true);
            try {
                values.enrollment_date = enrollmentDate;
                values.date_of_birth = dob;

                let response = await memberDetails.MemberDetailModel.saveMemberDetails(values);
                setisShowLoader(false);
                if (response.statusCode == 200) {
                    formik.resetForm();
                    setShowToast({ isShow: true, type: "bg-success", message: response.body.message })
                } else {
                    setShowToast({ isShow: true, type: "bg-danger", message: response.body.message })
                }

            } catch (error) {
                console.log(error.response.body.message);
                setisShowLoader(false);
                setShowToast({ isShow: true, type: "bg-danger", message: error.response.body.message })
            }
        },
        handleChange: () => {
            setValues(prevValues => ({
                ...prevValues,
                // we use the name to tell Formik which key of `values` to update
                [values.target.name]: values.target.value
            }))
        },
    });
    return (
        <>
            <Toast key={1} autohide delay={3000}  show={showToast.isShow} onClose={() => setShowToast({ isShow: false, type: "", message: "" })} className={"loader " + (showToast.type)} >
                <Toast.Header>
                    <strong className="me-auto">{showToast.type == "bg-danger" ? "Error" : "Success"} Message</strong>
                </Toast.Header>
                <Toast.Body className="Dark">
                    {showToast.message}
                </Toast.Body>
            </Toast>

            <Loader show={isShowLoader} />
            <h2 className="text-info text-center">Add Member</h2>
            <Container fluid className="bg-white mt-5 shadow-lg p-3 mb-5 bg-white rounded">

                <Form onSubmit={formik.handleSubmit}>
                    <Row className="mb-3" >
                        <Form.Group as={Col} className="form-group required" controlId="formGridEnroll">
                            <Form.Label >Enrollment Date</Form.Label>
                            <DatePicker class="form-control"
                                selected={enrollmentDate}
                                onChange={(date) => setEnrollmentDate(date)}
                                name="enrollment_date"
                            />
                        </Form.Group>

                        <Form.Group as={Col} className="form-group required" controlId="formGridName">
                            <Form.Label >Member Name</Form.Label>
                            <Form.Control type="text"
                                placeholder="member name"
                                name="member_name"
                                onChange={formik.handleChange}
                                value={formik.values.member_name}

                            />
                            {formik.touched.member_name && formik.errors.member_name ? (
                                <div class="text-danger">{formik.errors.member_name}</div>
                            ) : null}
                        </Form.Group>
                    </Row>
                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGriddob">
                            <Form.Label>Date of Birth</Form.Label>
                            <DatePicker
                                class="form-control"
                                selected={dob}
                                onChange={(date) => setDob(date)}
                                name="date_of_birth"
                            />
                        </Form.Group>
                        <Form.Group as={Col} className="mb-3" controlId="formGridAddress1">
                            <Form.Label>Gender</Form.Label>
                            <select
                                className="form-control"
                                name="gender"
                                onChange={formik.handleChange}
                                value={formik.values.gender}
                            >
                                <option key="" value="">Select Gender</option>

                                <option key="male" value="male">Male</option>
                                <option key="female" value="female">Female</option>

                            </select>
                        </Form.Group>

                    </Row>
                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGridAge">
                            <Form.Label>Age</Form.Label>
                            <Form.Control
                                name="age"
                                onChange={formik.handleChange}
                                value={formik.values.age}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridState">
                            <Form.Label>Marital Status</Form.Label>
                            <Form.Control
                                name="marital_status"
                                onChange={formik.handleChange}
                                value={formik.values.merital_status}
                            />
                        </Form.Group>

                    </Row>

                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGridAge">
                            <Form.Label>Phone number</Form.Label>
                            <Form.Control
                                name="mobile_number"
                                type="number"
                                onChange={formik.handleChange}
                                value={formik.values.mobile_number}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridState">
                            <Form.Label>Email</Form.Label>
                            <Form.Control
                                name="email_id"
                                onChange={formik.handleChange}
                                value={formik.values.email_id}
                                type="text"
                            />
                        </Form.Group>

                    </Row>
                    <Row className="mb-3">
                        <Form.Group as={Col} md={6} className="mb-3" controlId="formGridAddress1">
                            <Form.Label>Member Group</Form.Label>
                            <select className="form-control" name="member_group_id" onChange={formik.handleChange}
                                value={formik.values.member_group_id}
                            >
                                <option key="" value="">Select Member Group</option>
                                {groups.map((group,id)=>(
                                    <option key={group.group_code} value={group.group_code}>{group.group_code}</option>
                                ))}
                            </select>
                        </Form.Group>

                    </Row>
                    <Row className="mb-3">
                        <Col>
                            <h3 className="text-info">KYC Information</h3>
                        </Col>
                    </Row>
                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGridAge">
                            <Form.Label>Aadhar no.</Form.Label>
                            <Form.Control
                                name="aadhar_number"
                                type="number"
                                onChange={formik.handleChange}
                                value={formik.values.aadhar_number}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridState">
                            <Form.Label>PAN</Form.Label>
                            <Form.Control
                                name="pan_card_number"
                                onChange={formik.handleChange}
                                value={formik.values.pan_card_number}
                            />
                        </Form.Group>

                    </Row>
                    <Row className="mb-3" >
                        <Form.Group as={Col} controlId="formGridAge">
                            <Form.Label>Driving Licence No.</Form.Label>
                            <Form.Control
                                name="driving_license_number"
                                onChange={formik.handleChange}
                                value={formik.values.driving_license_number}
                            />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridState">
                            <Form.Label>Voter Id No.</Form.Label>
                            <Form.Control
                                name="voter_id_number"
                                onChange={formik.handleChange}
                                value={formik.values.voter_id_number}
                            />
                        </Form.Group>

                    </Row>
                    <Row className="mb-3" >
                        <Form.Group as={Col} md={6} controlId="formGridAge">
                            <Form.Label>Ration Card No.</Form.Label>
                            <Form.Control
                                name="ration_card_number"
                                onChange={formik.handleChange}
                                value={formik.values.ration_card_number}
                            />
                        </Form.Group>

                    </Row>
                    <Button variant="primary" type="submit">
                        Save
                    </Button>
                </Form>
            </Container>
        </>
    );
}

export default AddMember;