import {requests, superagent, API_ROOT} from "./BaseUrl"

//const url = '/memberGroups/';

const MemberGroupModel = {
  getMemberGroups(){
    return superagent.get(`${API_ROOT}/memberGroups/entry`)
  },
  saveMemberGroups(data){
    return superagent.post(`${API_ROOT}/memberGroups/entry`,data)
  }
  }
    export default {MemberGroupModel};