import React, { useEffect, useState } from 'react';
import { Table,Button, Container, Row, Col,Modal } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import memberRecord from '../../../models/memberDetails';
import Loader from '../../layout/Loader';
import moment from 'moment';
function Members(props) {
    const [isShowLoader, setisShowLoader] = useState(false)
    const [showDeleteModel, setShowDeleteModel] = useState(false)
    const [deleteID, setDeleteID] = useState(0)
const [memberRecords,setMemberRecords]=useState([]);
    useEffect(() => {
            getMemberGroup();
    },[])

    const getMemberGroup = () =>{
        setisShowLoader(true);
        memberRecord.MemberDetailModel.getMemberDetailsData().then(res=>{
            setisShowLoader(false);
            if(res.statusCode == 200){
                setMemberRecords(res.body.message);
            }else{
                setMemberRecords([]);
            }
        })
    }
    const deleteHandle=(id)=>{
        setDeleteID(id);        
        setShowDeleteModel(true);
    }
    const deleteMember=async()=>{
        setShowDeleteModel(false);
        try{
            setisShowLoader(true);
            let response = await memberRecord.MemberDetailModel.deleteMember(deleteID);
            setisShowLoader(false);
            if(response.statusCode == 200){
                let UpdatedList = memberRecords.filter((member,idx)=>member.member_id!=deleteID);
                setMemberRecords(UpdatedList);
             }
        }catch (error) {
                console.log(error.response.body.message);
                setisShowLoader(false);
            }
              
    }

    return (
        <>
      <Modal show={showDeleteModel} onHide={()=>setShowDeleteModel(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Delete Member</Modal.Title>
        </Modal.Header>
        <Modal.Body>Are you sure?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={()=>setShowDeleteModel(false)}>
            Cancel
          </Button>
          <Button variant="primary" onClick={()=>deleteMember()}>
            Delete
          </Button>
        </Modal.Footer>
      </Modal>        
        <Loader show={isShowLoader}/>
        <Container fluid>
        <h2 className="text-info text-center">Members</h2>
            <Row className="mb-5 mt-3">
                <Col>
                <Link to="/addMember">
                <Button variant="info" size="lg" active>
                    Add Member
                </Button>
                </Link>
                </Col>
            </Row>
            <Row>
            <Col>
            <Table className="shadow-lg p-3 mb-5 bg-white rounded" striped bordered hover responsive>
            <thead>
                <tr>
                    <th>Enrollment Date</th>
                    <th>Member Name</th>
                    <th>Member Id</th>
                    <th>PAN Number</th>
                    <th>Mobile Number</th>
                    <th>Action</th>
                </tr>
            </thead>
            <tbody>
                {memberRecords.map((group,id)=>(<tr>
                    <td>{moment(group.enrollment_date).format("DD-MM-YYYY HH:SS") }</td>
                    <td>{group.member_name}</td>
                    <td>{group.member_id}</td>
                    <td>{group.pan_card_number }</td>
                    <td>{group.mobile_number }</td>
                    <td><Link to = "/viewMemberDetail">
                    <Button size={"sm"} variant="primary">View</Button>
                        
                        </Link>{' '}
                    <Button size={"sm"} variant="danger" onClick={()=>deleteHandle(group.member_id)} >Delete</Button>
                        
                        </td>
                </tr>))}
            </tbody>
        </Table>
</Col>
            </Row>
        </Container>
        </>
        );
}

export default Members;