import React, { Component } from 'react'
import agent from '../../agent'
import Loader from '../layout/Loader';
import {  Card, Col, Row } from 'react-bootstrap';

export default function Dashboard() {
    return (
        <div >
            <Row xs={1} md={3} className="g-2">
                <Col>
                    <Card
                    bg={"primary"}
                    key={0}
                    text={'dark'}
                    style={{ width: '18rem' }}
                    className="m-2"
                    >
                        <Card.Body>
                        <Card.Title><h2 class="text-center">100</h2></Card.Title>
                        <Card.Text class="text-center">
                            Received Amount(Rs)
                        </Card.Text>
                        </Card.Body>
                    </Card> 
                </Col>

                <Col>
                    <Card
                    bg={"danger"}
                    key={0}
                    text={'dark'}
                    style={{ width: '18rem' }}
                    className="m-2"
                    >
                        <Card.Body>
                        <Card.Title><h2 class="text-center">10</h2> </Card.Title>
                        <Card.Text class="text-center">
                          Paid Amount(Rs)
                        </Card.Text>
                        </Card.Body>
                    </Card>            
                </Col>
                <Col>
                    <Card
                    bg={"success"}
                    key={0}
                    text={'dark'}
                    style={{ width: '18rem' }}
                    className="m-2"
                    >
                        <Card.Body>
                        <Card.Title><h2 class="text-center">90</h2> </Card.Title>
                        <Card.Text class="text-center">
                            Cash in hand(Rs)
                        </Card.Text>
                        </Card.Body>
                    </Card>            
                </Col>
            </Row>

        </div>
    )
}
