import React, { Component}  from 'react'
import { Button, Form, Col, Alert } from 'react-bootstrap';
import { Route, NavLink, Link,useHistory } from 'react-router-dom'
import { connect } from 'react-redux';
import agent from '../../agent'
import ListErrors from '../layout/ListErrors';

import {
    UPDATE_FIELD_AUTH,
    LOGIN,
    LOGIN_PAGE_UNLOADED
} from '../../constants/actionTypes';

class Login extends Component {
   // let history = useHistory();

    constructor(props) {
        super(props);
        this.changeEmail = ev => {this.props.onChangeEmail(ev.target.value)};
        this.changePassword = ev => {this.props.onChangePassword(ev.target.value)};
        this.submitForm = (email, password) => ev => {
            ev.preventDefault();
            this.props.onSubmit(email, password);
        };
    }
    componentDidUpdate(){
        if(this.props.isLoginSuccess){
            this.props.history.push('/')
        }
        
    }
    
    componentWillUnmount() {
        this.props.onUnload();
      }
    render() {
        const email = this.props.email;
        const password = this.props.password;
        return (
            <>
            <div className="login-div">
        {/* <Alert key={this.state.errormsg} variant="danger">{this.state.errormsg}</Alert> */}
            <ListErrors errors={this.props.errors} statusCode={this.props.statusCode}/>
                <Form className="login-form" onSubmit={this.submitForm(email, password)}>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicEmail">
                            <Form.Label>Email address</Form.Label>
                            <Form.Control type="email" placeholder="Enter email" value={email} onChange={this.changeEmail} required/>
                            <Form.Text className="text-muted">
                                We'll never share your email with anyone else.
                    </Form.Text>
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" placeholder="Password" value={password}  onChange={this.changePassword} required/>
                        </Form.Group>

                    </Form.Row>
                    <Form.Row>
                        <Button variant="primary" disabled={this.props.inProgress} type="submit">{this.props.inProgress ? 'Loading' : "Login"}
                            
                    </Button>
                    </Form.Row>
                    <Form.Row>
                        <Link as={Col} to="/registration" > SignUp</Link>
                    </Form.Row>

                </Form>
            </div>
            </>
        )
    }
}
const mapStateToProps = state => ({ ...state.auth });
const mapDispatchToProps = dispatch => ({
    onChangeEmail: value =>
        dispatch({ type: UPDATE_FIELD_AUTH, key: 'email', value }),
    onChangePassword: value =>
        dispatch({ type: UPDATE_FIELD_AUTH, key: 'password', value }),
    onSubmit: (email, password) =>
        dispatch({ type: LOGIN, payload: agent.Auth.login(email, password) }),
    onUnload: () =>
        dispatch({ type: LOGIN_PAGE_UNLOADED })
});

export default connect(mapStateToProps, mapDispatchToProps)(Login);
